.galery {
    width: 1080px;
    margin: 55px auto;
    display: flex;
    flex-wrap: wrap; }

.galery-item {
    width: 350px;
    height: 350px;
    margin-right: 15px;
    margin-bottom: 15px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    &:hover:before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #4c4c4c;
        opacity: .7;
        z-index: 10; } }


.galery-item:nth-child(3n) {
    margin: 0; }

.go-back {
    text-align: center;
    text-decoration: none;
    display: block;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: #aaa;
    border: 2px solid #000;
    color: #000;
    font-size: 18px;
    font-weight: bold;
    line-height: 40px;
    margin: 70px auto;
    transition: .4s; }

.go-back:hover {
    transition: .4s;
    background-color: rgb(196, 105, 121); }


.go-back:before {
    margin: 0; }
