.slider {
    max-height: 100vh;
    overflow: hidden; }

.slick-arrow {
    position: absolute;
    width: 100px;
    height: 100%;
    display: block;

    transform: translateY(-50%);
    border: none;
    opacity: .5;
    background-color: transparent;
    top: 50%;
    cursor: pointer;
    transition: .4s;
    z-index: 1;
    outline: 0; }

.slick-arrow:hover {
    transition: .4s;
    opacity: 1; }

.slick-prev {
    left: 0; }

.slick-next {
    right: 0; }

.slick-prev:before, .slick-next:before {
        content: '';
        display: block;
        width: 30px;
        height: 30px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-30%, -50%) rotate(-135deg);

        border-top: 3px solid rgb(196, 105, 121);
        border-right: 3px solid rgb(196, 105, 121); }

.slick-next:before {
        transform: translate(-70%, -50%) rotate(45deg); }

.slick-dots {
    display: flex;
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
    li {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        cursor: pointer;
        background-color: rgb(196, 105, 121);
        margin: 0 5px;
        transition: 1.5s; }
    button {
        cursor: pointer;
        opacity: 0; }

    .slick-active {
        transition: 1.5s;
        transform: scale(1.4); } }
