.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 80%;
    width: 100%;
    z-index: 20;
    display: none; }


.modal.is-show,
.overlay.is-show {
    display: block; }

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .9);
    z-index: 10;
    display: none; }

.flaticon-cross-out {
    right: 0; }

.flaticon-minimize,
.flaticon-expand {
    left: 0; }

.flaticon-minimize,
.flaticon-expand,
.flaticon-cross-out {
    position: absolute;
    top: 0;
    color: #666;
    cursor: pointer;
    transition: .4s;
    margin: 0;
    font-weight: bold;
    coursor: pointer;
    padding: 20px;
    z-index: 50;
    &:hover {
        transition: .4s;
        color: #111; } }

.flaticon-minimize:before,
.flaticon-expand:before,
.flaticon-cross-out:before {
    margin: 0;
    z-index: 50;
    font-size: 28px; }

.modal__cross__dark {
    color: #aaa; }

.modal__cross__dark:hover {
    color: #fff; }

.modal__cross,
.modal__full {
    position: absolute;
    width: 50%;
    height: 70%;
    top: -30%; }

.modal__cross {
    right: 0; }

@media (max-width: 900px) {
    .flaticon-minimize,
    .flaticon-expand,
    .flaticon-cross-out {
        padding: 10px; }

    .flaticon-minimize:before,
    .flaticon-expand:before,
    .flaticon-cross-out:before {
        font-size: 14px; }

    .slick-arrow:before {
        width: 15px;
        height: 15px; } }


.slider-modal {
    height: 80vh;
    width: 100%;
    img {
        height: 80vh !important;
        width: 100%;
        object-fit: contain; }

    .slick-arrow {
        width: 50%; }

    .slick-prev:before {
        left: 0;
        transform: translate(100%, -50%) rotate(-135deg); }

    .slick-next:before {
        right: 0;
        left: auto;
        transform: translate(-100%, -50%) rotate(45deg); } }



.modal-video-wrap {
    width: 80%;
    height: 100%;
    margin: auto; }

.video-block {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden; }

.video-block iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }


//Contact
.contact-modal {
    form {
        display: flex;
        height: 100%;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        input {
            width: 50%;
            margin: 15px 0; } } }
